<script>
import { Modal } from 'bootstrap';
// eslint-disable-next-line import/no-extraneous-dependencies
import VueDatePicker from '@vuepic/vue-datepicker';
// eslint-disable-next-line import/no-extraneous-dependencies
import '@vuepic/vue-datepicker/dist/main.css';

export default {
  data() {
    return {
      modal: '',
      isLoading: false,
      flow: ['year', 'month', 'calender'],
      tempData: {
        title: 'lulu',
        category: 'ooo',
        origin_price: 900,
        price: 12,
        unit: '',
        description: 'Sit down please ',
        content: 'luu so cute',
        is_enabled: 1,
        manufacturer: '',
        worker: [],
        address: '',
        date: '',
        imageUrl: 'https://fakeimg.pl/100/',
      },
    };
  },
  components: {
    VueDatePicker,
  },
  methods: {
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.modal.hide();
    },
    createEdit() {
      this.isLoading = true;
      const api = 'https://ec-course-api.hexschool.io/v2/api/testqq/admin/product';
      this.$http.post(api, { data: this.tempData }).then((res) => {
        if (res.data.success) {
          this.isLoading = false;
          this.tempData.manufacturer = '';
          this.tempData.worker = [];
          this.tempData.address = '';
          this.tempData.date = '';
          this.tempData.imageUrl = '';
          this.hideModal();
          this.$emit('getData');
        }
      });
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.workModal);
  },
};
</script>
