<template>
  <section class="home">
    <!-- Button trigger modal -->
    <button type="button" class="btn btn-primary" @click="openModal()"></button>
    <CreateModel ref="openModal" :product="product" @get-data="getData" />
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in workOrder" :key="index">
          <tr>
            <th scope="row">{{ index }}</th>
            <td><span class="text-danger text-nowrap"></span></td>
            <td class="text-nowrap">{{ item.manufacturer }}</td>
            <td class="">{{ item.address }}</td>
            <td class="text-nowrap">{{ item.date }}</td>
            <td class="text-nowrap">{{ item.worker }}</td>
            <td><img :src="item.imageUrl" alt="" /></td>
          </tr>
        </template>
      </tbody>
    </table>
    <div class="d-flex justify-content-center">
      <div class="spinner-border" :class="{ 'd-none': !isLoading }" role="status">
        <span class="visually-hidden">Loading...</span>
      </div>
    </div>
  </section>
</template>

<script>
// @ is an alias to /src
import CreateModel from '@/components/CreateModel.vue';

export default {
  data() {
    return {
      isLoading: false,
      isNew: false,
      product: {},
      workOrder: [],
    };
  },
  name: 'HomeView',
  components: {
    CreateModel,
  },
  methods: {
    checkAuth() {
      const token = document.cookie.replace(
        /(?:(?:^|.*;\s*)testToken\s*=\s*([^;]*).*$)|^.*$/,
        '$1',
      );
      if (token) {
        this.$http.defaults.headers.common.Authorization = `${token}`;
        const api = 'https://ec-course-api.hexschool.io/v2/api/user/check';
        this.$http.post(api, { api_token: this.token }).then((res) => {
          if (res.data.success) {
            this.getData();
          }
        });
      } else {
        alert('');
        this.$router.push('./login');
      }
    },
    getData() {
      this.isLoading = true;
      const api = 'https://ec-course-api.hexschool.io/v2/api/testqq/admin/products/all';
      this.$http
        .get(api)
        .then((res) => {
          this.workOrder = Object.values(res.data.products);
          this.workOrder.forEach((item) => {
            // eslint-disable-next-line no-param-reassign
            item.date = item.date.substr(0, 10);
          });
          this.isLoading = false;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.product = {
          imagesUrl: [],
        };
      } else {
        this.product = { ...item };
      }
      this.$refs.openModal.showModal();
    },
  },
  mounted() {
    this.checkAuth();
  },
};
</script>
